html{
  height: 100%;
}
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: rgb(251 236 216);
  width: 100%;
  height: 100%;
}
.container{
  max-width: 480px;
  /* height: 100%; */
  background: #fff;
  margin: 0 auto;
  height: 100%;
  /* height: calc(var(--vh, 1vh) * 100); */
  box-shadow: 1px 2px 5px #594830;
}
.container .header{
  display: flex;
  justify-content: space-between;
  /* background: #777; */
  padding: 10px 20px;
  align-items: center;
}
.container .header h2{
  line-height: 10px;
}
.container .header a{
  text-decoration: none;
  color: black;
}
.container .header .login{
  color: #594830;
  cursor: pointer;
}
.main_img{
  max-width: 440px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}
.main_img img{
  /* object-fit: cover; */
  max-width: 360px;
  text-align: center;
  height: 300px;
}
.shopList_btn{
  padding: 20px 20px;
}
.login_btn{
  padding: 20px 0px;
}
.btn_text{
  width: 100%;
  text-align: center;
  padding: 30px 0;
  background-color: #A78860;
  cursor: pointer;
  border-radius: 6px;
  font-size: 1.5rem;
  letter-spacing: 3px;
  font-weight: bold;
  color: #fff;
  border: none;
}
.main_text{
  padding: 0 20px;
  font-size: 1.5rem;
  font-weight: 500;
}
.main_text .main_shop{
  margin-left: 24px;
  font-size: 2rem;
  color: #D72E2E;
  font-weight: 600;
}
.sub_text{
  text-align: center;
  font-size: 1.5rem;
}
.sub_text .time{
  font-size: 4rem;
}
.wait_content{
  padding: 10px;
  display: flex;
  justify-content: space-between;
}
.wait_content .wait_text{
  flex: 1;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 50%;
  font-size: 2rem;
  background-color: #A78860;
  border-radius: 6px 0 0 6px ;
  color: #fff;
  line-height: 40px;
  display: flex;
  text-align: center;
  align-items: center;
}
.wait_content .wait_number{
  flex: 1;
  padding: 40px 0;
  text-align: center;
  justify-content: center;
  font-size: 5rem;
  background-color: #C2AC90;
  border-radius: 0 6px 6px 0;
  display: flex;
}
.wait_content .wait_number .notshow{
  display: none;
}
.wait_content .wait_number .showtime{
  display: inline-block;
}
.minute{
  font-size: 1rem;
  bottom: 0;
  top: -10px;
}
.wait_control{
  padding: 20px;
  display: flex;
  font-size: 6rem;
  justify-content: space-between;
  align-items: center;
}
.wait_control .wait_plus{
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  text-align: center;
  flex: 1;
  background: #A78860;
  border-radius: 50%;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  box-shadow: 1px 2px 3px 2px #594830;
}
.wait_control .wait_plus.clicked{
  box-shadow:none;
}
.wait_control .wait_minus{
  margin: 0 10px;
  display: flex;
  justify-content: center;
  align-items:center;
  width: 200px;
  height: 200px;
  text-align: center;
  flex: 1;
  background: #C2AC90;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 1px 2px 3px 2px #594830;
}
.wait_control .wait_minus.clicked{
  box-shadow:none;
}
.wait_control .wait_minus.click{
  box-shadow: none;
}
.wait_control .wait_plus.click{
  box-shadow: none;
}
.shoplist_title, .login_title{
  margin: 0 20px;
  font-size: 1.5rem;
  font-weight: bold;
  color: #7A5627;
}
.shoplist{
  margin: 15px;
  padding: 15px 15px 1px 15px;
  background-color: #dfd9d1;
  border-radius: 4px;
  cursor: pointer;
}
.shopitem{
  border: 2px solid #7A5627;
  margin-bottom: 15px;
  display: flex;
  flex-direction: row;
  border-radius: 6px;
}
.shopitem .shopname{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  background-color: #A78860;
}
.shopitem .shopwait{
  flex: 1;
  padding: 10px 0;
  text-align: center;
  justify-content: center;
  background-color: #C2AC90;
  font-size: 2rem;
}
.loginform{
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.loginform .loginId,.loginform .loginpw{
  font-size: 1.4rem;
}
.inputId, .inputPw{
  font-size: 1.5rem;
  padding: 20px 10px;
  border-radius: 6px;
  flex: 1;
  width: 100%;
  margin-right: 20px;
  box-sizing: border-box;
  margin-bottom: 10px;
}
@media screen and (max-width: 480px) {
  .wait_control .wait_plus{
    width: 147px;
    height: 147px;
    align-items: none;
  }
  .wait_control .wait_minus{
    width: 147px;
    height: 147px;
    align-items: none;
  }
}